.workBlock {
    display: flex;
    flex-direction: column;
    background-color: greenyellow;
    max-width: 400px;
}

.projectImg {
    width: 100%;
    height: 270px;
    justify-content: center;
    align-items: center;
    display: flex;
}

.watchButton {
    width: 110px;
    height: 40px;
}

.description {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 5px 20px;
}

.description h3, p {
    margin: 0;
}
